<template>
  <v-row>
    <v-col cols="24" sm="12">
      <div class="row-heading">
        {{ $t("upload_csv") }}
      </div>
    </v-col>
    <v-col cols="24" sm="12">
      <div class="upload-file-wrapper">
        <div class="upload-file-left">
          <input id="file" ref="file" type="file" @change="handleFileUpload" />
        </div>
        <div v-if="file && headerDetected" class="upload-file-right">
          <strong>Header detected:</strong> first line of the file is a list of column names.
          <br />
          <!-- <strong>No header detected:</strong> fist line of the file is NOT a
          list of column names. -->
          <v-checkbox
            v-model="disableHeader"
            label="Disable headers, first line is not a list column names"
            @change="reProcessFileDisableHeader"
          />
        </div>

        <div v-if="file && !headerDetected" class="upload-file-right">
          <strong>No header detected:</strong> fist line of the file is NOT a list of column names.
          <br />
          <!-- <strong>No header detected:</strong> fist line of the file is NOT a
          list of column names. -->
          <v-checkbox
            v-model="enableHeader"
            label="Enable headers, first line is a list column names"
            @change="reProcessFileEnableHeader"
          />
        </div>
        <div style="clear: both; display: block; width: 100%"></div>
      </div>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    headerDetected: {
      type: Boolean,
      required: false,
      default: () => false
    }
  },
  data() {
    return {
      disableHeader: false,
      enableHeader: false,
      file: null
    };
  },
  methods: {
    handleFileUpload(event) {
      event.preventDefault();
      const files = event.target.files || event.dataTransfer.files;
      const file = files[0];
      this.file = file;
      this.$emit("handleFileUpload", {
        file,
        disableHeader: this.disableHeader,
        enableHeader: this.enableHeader
      });
    },
    reProcessFileEnableHeader() {
      this.disableHeader = false;
      this.$emit("handleFileUpload", {
        file: this.file,
        disableHeader: this.disableHeader,
        enableHeader: this.enableHeader
      });
    },
    reProcessFileDisableHeader() {
      this.enableHeader = false;
      this.$emit("handleFileUpload", {
        file: this.file,
        disableHeader: this.disableHeader,
        enableHeader: this.enableHeader
      });
    }
  }
};
</script>
<style lang="scss">
.upload-file-wrapper {
  border: 1px solid #d8dbeb;
  border-radius: 10px;
  padding: 10px;
  width: 100%;
}

.upload-file-left {
  display: inline-block;
  width: 40%;
  vertical-align: middle;
}
.upload-file-right {
  display: inline-block;
  width: 60%;
  margin-top: 17px;
  vertical-align: middle;
  .v-input--selection-controls {
    margin-top: 0;
  }
}
</style>
