import Vue from "vue";
import Vuex from "vuex";

import addon from "./modules/addon";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    errorPopupVisible: false,
    erorPopupRedirectVisible: false,
    popupPosition: 650
  },
  getters: {
    errorPopupVisible: state => state.errorPopupVisible,
    erorPopupRedirectVisible: state => state.erorPopupRedirectVisible,
    popupPosition: state => state.popupPosition
  },
  mutations: {
    STORE_ERROR_POPUP_VISIBLE(state, data) {
      state.errorPopupVisible = data;
    },
    STORE_ERROR_REDIRECT_POPUP_VISIBLE(state, data) {
      state.erorPopupRedirectVisible = data;
    }
  },

  modules: {
    addon
  }
});
