<template>
  <div>
    <v-row v-if="headersWithDataType.length">
      <v-col cols="12" sm="6">
        <div ref="topelement" />
        <label class="as">{{ $t("detected_as") }}</label>
      </v-col>
      <v-col cols="12" sm="6">
        <label class="as">{{ $t("imported_as") }}</label>
      </v-col>
    </v-row>
    <div
      v-for="headerWithDataType in headersWithDataType"
      :key="headerWithDataType.header"
      class="loop-row"
    >
      <v-col cols="12" sm="6">
        <input type="text" class="detected-as-input" :value="headerWithDataType.header" disabled />
      </v-col>
      <v-col cols="12" sm="6">
        <select
          :value="headerWithDataType.dataType"
          class="imported-as-drop-down"
          :class="{
            ignored: headerWithDataType.dataType === 'ignored'
          }"
          @change.prevent="changeDataType($event, headerWithDataType.header)"
        >
          <option v-for="dataType in dataTypes" :key="dataType.value" :value="dataType.value">
            {{ dataType.text }}
          </option>
        </select>
      </v-col>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    headersWithDataType: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      dataTypes: [
        { text: this.$t("date"), value: "date" },
        { text: this.$t("amount"), value: "currency_amount" },
        { text: this.$t("depsit_amount"), value: "deposit_amount" },
        { text: this.$t("withdraw_amount"), value: "withdrawal_amount" },
        { text: this.$t("type"), value: "type" },
        { text: this.$t("asset"), value: "asset" },
        { text: this.$t("category"), value: "category" },
        { text: this.$t("description"), value: "description" },
        { text: this.$t("fee"), value: "fee" },
        { text: this.$t("institution"), value: "institution" },
        { text: this.$t("investment"), value: "investment" },
        { text: this.$t("note"), value: "note" },
        { text: this.$t("quantity"), value: "quantity" },
        { text: this.$t("gain"), value: "realized_gain" },
        { text: this.$t("security"), value: "security" },
        { text: this.$t("settlement_date"), value: "settlement_date" },
        { text: this.$t("symbol"), value: "symbol" },
        { text: this.$t("ignored"), value: "ignored" }
      ]
    };
  },
  methods: {
    async changeDataType(event, header) {
      const dataType = event.target.value;
      this.$emit("changeDataType", { dataType, header });
    }
  }
};
</script>
