import { dataTypes } from "../constants/datatypes";
import { isDataTypeCorrect, parseValue } from "./helpers";

const findDataType = (key, value) => {
  const matchingDataTypes = dataTypes.filter(
    dataType =>
      dataType.matching_words.some(v => key.toLowerCase() === v.toLowerCase()) &&
      isDataTypeCorrect(value, dataType.type)
  );
  const [foundDatType] = matchingDataTypes;
  return { header: key, dataType: foundDatType ? foundDatType.name : "ignored" };
};

const getSelectedDataTypeAndValue = (headersWithDataType, dataType, _value) => {
  const headerObject = headersWithDataType.filter(
    headerDataType => headerDataType.header === dataType
  );
  return {
    dataType: headerObject[0].dataType,
    newValue: parseValue(headerObject[0].dataType, _value)
  };
};

export { findDataType, getSelectedDataTypeAndValue };
