<template>
  <Popup class="error-popup" @hide="hide">
    <!-- <template v-slot:title>{{ $t("error_happened_notified") }}</template> -->
    <template v-slot:content>
      <p v-if="errorMessage.length" class="error-popup__error">
        {{ errorMessage }}
      </p>
      <!-- <div v-if="errorStack" class="error-popup__stack">
        <code>{{ errorStack }}</code>
      </div> -->
      <!-- <p>{{ $t("we_apologize") }}</p> -->

      <button class="error-popup__button" @click="hide">
        {{ $t("close") }}
      </button>
    </template>
  </Popup>
</template>

<script>
import Popup from "./Popup.vue";

export default {
  components: {
    Popup
  },
  props: {
    error: {
      type: Object,
      required: false,
      default: () => null
    }
  },
  computed: {
    errorMessage() {
      if (!this.error) return "";
      if (this.error.data && this.error.data.message) return this.error.data.message;

      return this.error ? this.error.toString() : "";
    },
    errorStack() {
      return this.error ? this.error.stack : "";
    }
  },
  methods: {
    hide() {
      this.$store.commit("STORE_ERROR_POPUP_VISIBLE", false);
    }
  }
};
</script>

<style lang="scss">
.error-popup {
  z-index: 2010 !important;

  &__error {
    color: #ff4128;
  }

  &__stack {
    max-height: 200px;
    overflow-y: scroll;
    margin-bottom: 1rem;
  }

  &__button {
    @include btn-white-outlined;

    margin-top: 2rem;
    cursor: pointer;
  }
}
</style>
