export default (acceptedHeaders, $t) => {
  const selectedHeaders = acceptedHeaders.filter(
    element => element.dataType === "date" || element.dataType === "description"
  );

  const amountHeader = acceptedHeaders.filter(element => element.dataType === "currency_amount");

  const depositWithdrawalHeader = acceptedHeaders.filter(
    element => element.dataType === "deposit_amount" || element.dataType === "withdrawal_amount"
  );
  const typeHeader = acceptedHeaders.filter(element => element.dataType === "type");
  
  if (!amountHeader.length && !depositWithdrawalHeader.length)
    return { valid: false, message: $t("error_mandatory_amount") };

  if (amountHeader.length && depositWithdrawalHeader.length)
    return { valid: false, message: $t("error_you_choose") };

  if (amountHeader.length && !typeHeader.length)
    return { valid: false, message: $t("error_if_you_choose_amount") };

  if (amountHeader.length && typeHeader.length && selectedHeaders.length < 2)
    return { valid: false, message: $t("error_make_sure_amount") };

  if (depositWithdrawalHeader.length > 0 && selectedHeaders.length < 2)
    return { valid: false, message: $t("error_make_sure_deposit_amount") };

  return { valid: true, message: null };
};
