<template>
  <v-row>
    <v-col cols="24" sm="12">
      <div class="row-heading">{{ $t("import_to") }}</div>
    </v-col>
    <v-col cols="24" sm="12">
      <v-radio-group v-model="importTo" row>
        <v-radio value="institution" @click="updateImportTo">
          <template v-slot:label> {{ $t("institution") }} </template>
        </v-radio>
        <v-radio value="asset" @click="updateImportTo">
          <template v-slot:label> {{ $t("asset") }} </template>
        </v-radio>
      </v-radio-group>
    </v-col>
  </v-row>
</template>
<script>
export default {
  data() {
    return {
      importTo: "institution"
    };
  },
  methods: {
    updateImportTo() {
      this.$emit("updateImportTo", this.importTo);
    }
  }
};
</script>
