<template>
  <div>
    <error-popup v-if="errorPopupVisible" :error="error"> </error-popup>
    <error-redirect-popup
      v-if="erorPopupRedirectVisible"
      :failed-transactions="failedTransactions"
      :redirect-u-r-l="redirectURL"
      :show-go-to-saved-transactions="showGoToSavedTransactions"
    >
    </error-redirect-popup>
    <form enctype="multipart/form-data">
      <select-type @updateImportTo="updateImportTo" />

      <v-row>
        <v-col cols="24" sm="12">
          <div class="row-heading">
            <template v-if="importTo === 'institution'">
              {{ $t("select_institution") }}
            </template>
            <template v-else> {{ $t("select_asset") }} </template>
          </div>
        </v-col>
        <v-col cols="24" sm="12" class="select-institution-sec">
          <select-institution
            v-if="importTo === 'institution'"
            ref="selectInstitution"
            @updateInstitutionId="updateInstitutionId"
            @updateInvestmentId="updateInvestmentId"
          />
          <select-asset
            v-if="importTo === 'asset'"
            ref="selectAsset"
            @updateAssetId="updateAssetId"
          />
        </v-col>
      </v-row>

      <upload :header-detected="headerDetected" @handleFileUpload="handleFileUpload" />
      <v-row>
        <v-col cols="24" sm="12">
          <div class="row-heading">{{ $t("identify_columns") }}</div>
        </v-col>
        <v-col cols="24" sm="12">
          <div class="account-currency">{{ $t("account_currency") }}</div>
          {{ $t("selected_currency") }}
          <span class="selected-currency">{{ selectedCurrency._id }}</span>
          {{ $t("transactions_imported_as") }}
          <span class="selected-currency">{{ selectedCurrency._id }}</span>
        </v-col>
        <v-col cols="12" sm="6">
          <select-data-type
            ref="selectDataType"
            :headers-with-data-type="headersWithDataType"
            @changeDataType="changeDataType"
          />
        </v-col>
        <v-col cols="12" sm="6">
          <guidelines />
        </v-col>
      </v-row>

      <v-row v-if="headersWithDataType.length">
        <v-col cols="24" sm="12">
          <div class="row-heading">
            {{ $t("review_data") }}
          </div>
        </v-col>
        <v-col cols="24" sm="12" align="center" justify="center">
          <v-btn depressed color="primary" @click="uploadCSV">{{ $t("import") }}</v-btn>
        </v-col>
        <v-col cols="24" sm="12">
          <data-table
            :original-data-arr="originalDataArr"
            :accepted-headers="acceptedHeaders"
            :selected-transaction-columns="selectedTransactionColumns"
          />
        </v-col>
      </v-row>
    </form>
    <div class="text-center">
      <v-dialog v-model="dialog" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text>
            Saving your transactions! Please wait!
            <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { required } from "vuelidate/lib/validators";
import { parseCSV, originalCSV, getFileData } from "../utils/csv-util";
import { getSelectedDataTypeAndValue, findDataType } from "../utils/data-util";
import { isKeyIgnored, fromDate, toDate } from "../utils/helpers";
import validateDataType from "../utils/validation-helper";
import Guidelines from "./Guidelines.vue";
import DataTable from "./DataTable.vue";
import SelectType from "./SelectType.vue";
import SelectInstitution from "./SelectInstitution.vue";
import SelectAsset from "./SelectAsset.vue";
import Upload from "./Upload.vue";
import SelectDataType from "./SelectDataType.vue";
import ErrorPopup from "./overlay/ErrorPopup.vue";
import ErrorRedirectPopup from "./overlay/ErrorRedirectPopup.vue";

export default {
  name: "UploadForm",
  provide() {
    return {
      $v: this.$v
    };
  },
  components: {
    Guidelines,
    DataTable,
    SelectInstitution,
    SelectType,
    SelectAsset,
    Upload,
    SelectDataType,
    ErrorPopup,
    ErrorRedirectPopup
  },
  data() {
    return {
      importTo: "institution",
      account: null,
      institutionId: "",
      investmentId: "",
      assetId: "",
      file: null,
      headers: [],
      headersWithDataType: [],
      dataArr: [],
      originalDataArr: [],
      dialog: false,
      failedTransactions: [],
      redirectURL: "#",
      showGoToSavedTransactions: false,
      headerDetected: false,
      error: null
    };
  },
  validations: {
    institutionId: {
      required
    },
    investmentId: {
      required
    },
    assetId: {
      required
    }
  },
  computed: {
    ...mapGetters([
      "selectedCurrency",
      "savedTransactions",
      "errorPopupVisible",
      "erorPopupRedirectVisible"
    ]),
    selectedTransactionColumns() {
      return this.dataArr.map(data => {
        const newObj = {};
        Object.entries(data).forEach(entry => {
          const [key, value] = entry;
          if (!isKeyIgnored(this.headersWithDataType, key)) {
            newObj[key] = value;
          }
        });
        return newObj;
      });
    },
    importData() {
      return this.selectedTransactionColumns.map(data => {
        const newObj = {};
        Object.entries(data).forEach(entry => {
          const [key, value] = entry;
          const { dataType, newValue } = getSelectedDataTypeAndValue(
            this.acceptedHeaders,
            key,
            value
          );
          if (dataType === "deposit_amount" && newValue) {
            newObj.type = "deposit";
            newObj.currency_amount = newValue;
          } else if (dataType === "deposit_amount" && !newValue) {
            // do nothing
          } else if (dataType === "withdrawal_amount" && newValue) {
            newObj.type = "withdrawal";
            newObj.currency_amount = newValue;
          } else if (dataType === "withdrawal_amount" && !newValue) {
            // do nothing
          } else {
            newObj[dataType] = newValue;
          }
        });
        if (newObj.type === "withdrawal") {
          newObj.currency_amount = -newObj.currency_amount;
        }
        return newObj;
      });
    },

    acceptedHeaders() {
      return this.headersWithDataType.filter(item => item.dataType !== "ignored");
    }
  },

  methods: {
    ...mapActions(["saveTransactions"]),
    async handleFileUpload(payload) {
      const data = await getFileData(payload.file);
      const { originalDataArr, headerDetected } = originalCSV(
        data,
        payload.disableHeader,
        payload.enableHeader
      );
      this.headerDetected = headerDetected;
      this.originalDataArr = originalDataArr;
      this.dataArr = parseCSV(data, payload.disableHeader, payload.enableHeader);
      const headers = this.dataArr[0];
      const headersWithDataType = [];
      Object.entries(headers).forEach(([key, value]) => {
        const headerWithDataType = findDataType(key, value);
        headersWithDataType.push(headerWithDataType);
      });
      this.headersWithDataType = headersWithDataType;
    },
    async changeDataType({ dataType, header }) {
      this.headersWithDataType = this.headersWithDataType.map(headerDataType => {
        if (headerDataType.header === header) {
          return { header, dataType };
        }
        return { ...headerDataType };
      });
    },
    async validateInput() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        if (this.importTo === "institution") {
          if (this.$v.institutionId.$error) {
            const element = this.$refs.selectInstitution.$refs.institutionId;
            element.scrollIntoView({ behavior: "smooth" });
            return false;
          }
          if (this.$v.investmentId.$error) {
            const element = this.$refs.selectInstitution.$refs.investmentId;
            element.scrollIntoView({ behavior: "smooth" });
            return false;
          }
        } else if (this.$v.assetId.$error) {
          const element = this.$refs.selectAsset.$refs.assetId;
          element.scrollIntoView({ behavior: "smooth" });
          return false;
        }
      }
      return true;
    },

    async showErrorPopup(error) {
      this.error = error;
      this.$store.commit("STORE_ERROR_POPUP_VISIBLE", true);
    },

    async showErrorRedirectPopup() {
      this.$store.commit("STORE_ERROR_REDIRECT_POPUP_VISIBLE", true);
    },

    async uploadCSV() {
      const isInputValidated = await this.validateInput();
      if (!isInputValidated) {
        return;
      }
      const topElement = this.$refs.selectDataType.$refs.topelement;
      topElement.scrollIntoView({ behavior: "smooth" });
      const validationResponse = validateDataType(this.acceptedHeaders, this.$t);
      if (!validationResponse.valid) {
        await this.showErrorPopup({
          data: {
            message: validationResponse.message
          }
        });
        return;
      }

      this.dialog = true;

      const payload = {
        importTo: this.importTo,
        institutionId: this.institutionId,
        investmentId: this.investmentId,
        assetId: this.assetId,
        importData: this.importData
      };

      await this.saveTransactions(payload);
      if (this.savedTransactions && this.savedTransactions.length) {
        this.failedTransactions = this.savedTransactions.filter(transaction => transaction.error);
        if (this.savedTransactions.length !== this.failedTransactions.length) {
          this.showGoToSavedTransactions = true;
        }
        let redirectURL = "";
        if (this.importTo === "institution") {
          redirectURL = `institutions=${this.institutionId}`;
        } else {
          redirectURL = `assets=${this.assetId}`;
        }
        this.redirectURL = `https://app.wealthica.com/transactions?peek=true&${redirectURL}&from=${fromDate(
          this.importData
        )}&to=${toDate(this.importData)}`;
        this.dialog = false;
        if (this.failedTransactions.length) {
          await this.showErrorRedirectPopup();
        } else {
          setTimeout(() => {
            window.top.location.href = this.redirectURL;
          }, 200);
        }
      }
    },
    goToSavedTransactions() {
      window.top.location.href = this.redirectURL;
    },

    updateImportTo(value) {
      this.importTo = value;
    },
    updateInstitutionId(value) {
      this.institutionId = value;
    },
    updateInvestmentId(value) {
      this.investmentId = value;
    },
    updateAssetId(value) {
      this.assetId = value;
    }
  }
};
</script>

