<template>
  <div>
    <div v-if="assetId" class="institution-outer">
      <span v-if="assets.length" class="institution-inner">
        <img
          v-if="getAssetIcon(selectedAsset)"
          :src="getAssetIcon(selectedAsset)"
          alt="Wealthica"
        />

        <i v-else class="wicon" :class="iconClassName(selectedAsset.type)" />
      </span>
    </div>
    <div v-else class="institution-outer">
      <span v-for="asset of partAssets" :key="asset._id" class="institution-inner">
        <img v-if="getAssetIcon(asset)" :src="getAssetIcon(asset)" alt="Wealthica" />

        <i v-else class="wicon" :class="iconClassName(asset.type)" />
      </span>
      <span v-if="assets.length - partAssets.length - 1 > 0" class="institution-number"
        >{{ assets.length - partAssets.length - 1 }}+</span
      >
    </div>

    <select
      ref="assetId"
      v-model="assetId"
      class="account-drop-down"
      :class="{
        'account-drop-down-error': $v.assetId && $v.assetId.$error
      }"
      @change="updateAssetId"
    >
      <option selected value="">{{ $t("select_asset_to_import_to") }}</option>
      <option v-for="asset in assets" :key="asset._id" :value="asset._id">
        {{ asset.name }}
      </option>
    </select>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ASSET_TYPES from "../utils/assets";

export default {
  inject: ["$v"],
  data() {
    return {
      assetId: ""
    };
  },
  computed: {
    ...mapGetters(["assets"]),
    selectedAsset() {
      if (this.assetId) {
        const assets = this.assets.filter(asset => asset._id === this.assetId);
        return assets[0];
      }
      return this.assets[0];
    },
    partAssets() {
      if (this.assets.length >= 3) {
        return this.assets.slice(0, 2);
      }
      return this.assets;
    }
  },
  methods: {
    updateAssetId() {
      this.$emit("updateAssetId", this.assetId);
    },
    getAssetIcon(asset) {
      return asset.image || null;
    },
    iconClassName(type) {
      let icon;
      Object.entries(ASSET_TYPES).forEach(entry => {
        const [assetType] = entry;
        if (assetType === type) {
          icon = ASSET_TYPES[assetType].icon;
        } else if (ASSET_TYPES[assetType].sub_types && ASSET_TYPES[assetType].sub_types[type]) {
          icon = ASSET_TYPES[assetType].sub_types[type].icon || ASSET_TYPES[assetType].icon;
        }
      });
      return icon;
    }
  }
};
</script>
