import { lineToObject, checkFirstRowIsHeader } from "./helpers";

import { splitRegex } from "../constants/regex";

const splitLinesAndCustomHeader = (csv, disableHeader, enableHeader) => {
  let lines = csv.split("\n");
  // remove blank lines
  lines = lines.filter(line => line && line !== "\n" && line !== "\r" && line !== "\r\n");
  const firstRow = lines[0];
  const headerDetected =
    disableHeader || enableHeader ? !!enableHeader : checkFirstRowIsHeader(firstRow);
  let header = "";
  if (!headerDetected) {
    // push Col1 , Col2 etc.
    const firstRowData = firstRow.split(splitRegex);

    firstRowData.forEach((h, i) => {
      header = `${header}Col${i + 1}${i === firstRowData.length - 1 ? "" : ","}`;
    });
  }
  return { lines, headerDetected, header, firstRow };
};

const originalCSV = (csv, disableHeader, enableHeader) => {
  const response = splitLinesAndCustomHeader(csv, disableHeader, enableHeader);
  const { headerDetected, header } = response;
  let { lines } = response;

  if (!headerDetected) lines = [header, ...lines];
  const originalDataArr = lines.map(line => line.split(splitRegex));
  return { originalDataArr, headerDetected };
};

const parseCSV = (csv, disableHeader, enableHeader) => {
  const response = splitLinesAndCustomHeader(csv, disableHeader, enableHeader);
  const { lines, headerDetected, firstRow } = response;
  let { header } = response;
  if (headerDetected) {
    lines.shift();
    header = firstRow.split(splitRegex);
  } else {
    header = header.split(",");
  }
  return lines.map(line => lineToObject(header, line));
};

const getFileData = async file => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(atob(reader.result.split(",")[1]));
    reader.onerror = error => reject(error);
  });
};

export { parseCSV, originalCSV, getFileData };
