import moment from "moment";
import { splitRegex, amountRegex } from "../constants/regex";

export const isDate = value => {
  return new Date(value) !== "Invalid Date" && !Number.isNaN(new Date(value));
};
export const isNumeric = value => {
  if (typeof value !== "string") return false;
  return !Number.isNaN(value) && !Number.isNaN(parseFloat(value));
};

export const isString = value => {
  return typeof value === "string";
};

export const lineToObject = (header, line) => {
  const bits = line.split(splitRegex);
  const obj = {};
  header.forEach((h, i) => {
    obj[h] = bits[i];
  }); // or use reduce here
  return obj;
};

export const checkFirstRowIsHeader = firstRow => {
  const headers = firstRow.split(splitRegex);
  // check if columns are not string or empty;
  const nonStringColumn = headers.filter(header => {
    return typeof header !== "string";
  });
  if (nonStringColumn.length) return false;
  const dateColumn = headers.filter(
    header =>
      Number.isNaN(Number(header)) &&
      new Date(header) !== "Invalid Date" &&
      !Number.isNaN(Number(new Date(header))) &&
      moment(new Date(header)).isValid()
  );
  return !dateColumn.length;
};

export const isKeyIgnored = (headersWithDataType, header) => {
  const ignoredArray = headersWithDataType
    .filter(headerDataType => headerDataType.dataType !== "ignored")
    .map(headerDataType => headerDataType.header);
  return !ignoredArray.includes(header);
};
export const parseAmount = _value => {
  const value = _value;
  if (!value) return null;
  const matches = value.match(amountRegex);
  if (!matches || !matches.length) return null;
  const [firstMatch] = matches;
  return firstMatch.replace(/,/g, "");
};

export const parseValue = (dataType, _value) => {
  const value = _value;
  switch (dataType) {
    case "currency_amount":
      return parseAmount(value);

    case "deposit_amount":
      return parseAmount(value);

    case "withdrawal_amount":
      return parseAmount(value);

    case "date":
      return moment(value).format("YYYY-MM-DDTHH:mm:ss");

    case "type":
      return value.toLowerCase();

    default:
      return value;
  }
};

export const isDataTypeCorrect = (value, dataType) => {
  switch (dataType) {
    case "date-time":
      return isDate(value);
    case "number":
      return isNumeric(value);
    case "string":
      return isString(value);
    default:
      return false;
  }
};

export const fromDate = importData => {
  const mnDate = importData.reduce((a, b) => {
    return moment(b.date).diff(moment(a.date)) >= 0 ? a : b;
  });
  return moment(mnDate.date).format("YYYY-MM-DD");
};
export const toDate = importData => {
  const mnDate = importData.reduce((a, b) => {
    return moment(b.date).diff(moment(a.date)) >= 0 ? b : a;
  });
  return moment(mnDate.date).format("YYYY-MM-DD");
};
