<template>
  <Popup class="error-popup" @hide="hide">
    <template v-slot:title>{{ $t("failed_transactions_message") }}</template>
    <template v-slot:content>
      <p
        v-for="(failedTransaction, index) in failedTransactions"
        :key="index"
        class="error-popup__error"
      >
        {{ failedTransaction.error.message }}
      </p>
      <button v-if="showGoToSavedTransactions" class="error-popup__button" @click="hideAndRedirect">
        {{ $t("go_to_saved_transactions") }}
      </button>
    </template>
  </Popup>
</template>

<script>
import Popup from "./Popup.vue";

export default {
  components: {
    Popup
  },
  props: {
    failedTransactions: {
      type: Array,
      required: false,
      default: () => []
    },
    redirectURL: {
      type: String,
      required: false,
      default: () => null
    },
    showGoToSavedTransactions: {
      type: Boolean,
      required: false,
      default: () => false
    }
  },
  methods: {
    hideAndRedirect() {
      this.$store.commit("STORE_ERROR_REDIRECT_POPUP_VISIBLE", false);
      window.top.location.href = this.redirectURL;
    },
    hide() {
      this.$store.commit("STORE_ERROR_REDIRECT_POPUP_VISIBLE", false);
    }
  }
};
</script>

<style lang="scss">
.error-popup {
  z-index: 2010 !important;

  &__error {
    color: #ff4128;
  }

  &__stack {
    max-height: 200px;
    overflow-y: scroll;
    margin-bottom: 1rem;
  }

  &__button {
    @include btn-white-outlined;

    margin-top: 2rem;
    cursor: pointer;
  }
}
</style>
