<template>
  <div>
    <div v-if="institutionId" class="institution-outer">
      <span v-if="institutions.length" class="institution-inner">
        <img
          :src="`https://app.wealthica.com/images/institutions/${getInstitutionType}.png`"
          alt="Wealthica"
      /></span>
    </div>
    <div v-else class="institution-outer">
      <span v-for="institution of partInstitutions" :key="institution.id" class="institution-inner">
        <img
          :src="`https://app.wealthica.com/images/institutions/${institution.type}.png`"
          alt="Wealthica"
      /></span>
      <span v-if="institutions.length - partInstitutions.length - 1 > 0" class="institution-number"
        >{{ institutions.length - partInstitutions.length - 1 }}+</span
      >
    </div>
    <select
      ref="institutionId"
      v-model="institutionId"
      class="account-drop-down"
      :class="{
        'account-drop-down-error': $v.institutionId && $v.institutionId.$error
      }"
      @change="updateInstitutionId"
    >
      <option selected value="">Select an institution to import to.</option>
      <option v-for="institution in institutions" :key="institution.id" :value="institution.id">
        {{ institution.name }}
      </option>
    </select>

    <select
      v-if="institutionId"
      ref="investmentId"
      v-model="investmentId"
      class="account-drop-down"
      :class="{
        'account-drop-down-error': $v.investmentId && $v.investmentId.$error
      }"
      @change="updateInvestmentId"
    >
      <option selected value="">Select an investment to import to.</option>
      <option v-for="investment in investments" :key="investment._id" :value="investment._id">
        {{ investment.name }}
      </option>
    </select>
  </div>
</template>
<script>
import { mapGetters } from "vuex";

export default {
  inject: ["$v"],
  data() {
    return {
      institutionId: "",
      investmentId: ""
    };
  },
  computed: {
    ...mapGetters(["institutions", "assets"]),
    investments() {
      if (this.institutionId) {
        return this.institutions.filter(institution => institution.id === this.institutionId)[0]
          .investments;
      }
      return [];
    },
    partInstitutions() {
      if (this.institutions.length >= 3) {
        return this.institutions.slice(0, 2);
      }
      return this.institutions;
    },
    getInstitutionType() {
      if (!this.institutionId) {
        return this.institutions[0].type;
      }
      const institutions = this.institutions.filter(
        institution => institution.id === this.institutionId
      );
      return institutions[0].type;
    }
  },
  methods: {
    updateInstitutionId() {
      this.$emit("updateInstitutionId", this.institutionId);
    },
    updateInvestmentId() {
      this.$emit("updateInvestmentId", this.investmentId);
    }
  }
};
</script>
